
/* Header Section */
.intro {
    display: table;
    width: 100%;
    /*height: 100%;*/
    /*max-height: calc(100vh - 210px);*/
    /*background: url(../img/intro-bg.jpg) center center no-repeat;*/
    /*background: url('http://localhost:3000/an124/2.jpg') center center no-repeat;*/
    background-color: #e5e5e5;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    background-size: cover;
    -o-background-size: cover;
}
.intro .mainImg {
    /*max-width: 100%;*/
    max-height: calc(100vh - 236px);
    background-position: center;
    width: 100%;
    object-fit: cover;
    transition: all 0.3s;
}

.intro .overlay {
    background: rgba(0, 0, 0, 0.2);
}
.intro h1 {
    font-family: "Raleway", sans-serif;
    color: #fff;
    font-size: 82px;
    font-weight: 700;
    text-transform: uppercase;
    margin-top: 0;
    margin-bottom: 10px;
}
.intro h1 span {
    font-weight: 800;
    color: #5ca9fb;
}
.intro p {
    color: #fff;
    font-size: 22px;
    font-weight: 300;
    line-height: 30px;
    margin: 0 auto;
    margin-bottom: 60px;
}
header .intro-text {
    padding-top: 350px;
    padding-bottom: 200px;
    text-align: center;
}
